import * as React from 'react';
import { Button, Col, Row } from 'react-bootstrap';
import { FoodItem } from './FoodItem';

interface MenuContentProps {
  foodItems: FoodItem[];
  selectItem: Function;
}

class ItemChunk {
  id: number;
  foodItems: FoodItem[];

  constructor(id: number, foodItems: FoodItem[]) {
    this.id = id;
    this.foodItems = foodItems;
  }
}

class MenuContent extends React.Component<MenuContentProps> {
  splitItemsIntoChunks(foodItems: FoodItem[], chunkSize: number): ItemChunk[] {
    const chunkedItems: ItemChunk[] = [];

    let i, j;
    for (i = 0, j = foodItems.length; i < j; i += chunkSize) {
      const chunk = new ItemChunk(i, foodItems.slice(i, i + chunkSize));

      chunkedItems.push(chunk);
    }

    return chunkedItems;
  }

  render() {
    const { foodItems, selectItem } = this.props;
    const chunkedItems = this.splitItemsIntoChunks(foodItems, 4);

    return (
      <div>
        {chunkedItems.map(chunk => {
          const id = chunk.id;
          const items = chunk.foodItems;

          return (
            <Row key={id} className="menu-row">
              {items.map(item => {
                return (
                  <Col md={3} key={item.id}>
                    <Button
                      size="sm"
                      block={true}
                      href="#"
                      onClick={(event: React.MouseEvent<HTMLElement>) =>
                        selectItem(item)
                      }
                    >
                      {item.name}
                    </Button>
                  </Col>
                );
              })}
            </Row>
          );
        })}
      </div>
    );
  }
}

export default MenuContent;



