import * as React from 'react';
import { History } from 'history';
import { bindActionCreators, Dispatch, AnyAction } from 'redux';
import { connect } from 'react-redux';

import { AppgustoState } from 'src/store/State';
import { BrowserActionCreators, BrowserState } from 'src/browser/api';

interface LocationHandlerProps {
  browserState: BrowserState;
  changeGuest: Function;
  changePerson: Function;
  changeTable: Function;
  history: History;
}

class LocationHandler extends React.Component<LocationHandlerProps> {
  guestNrParameter: string = 'guestNr';
  tableNrParameter: string = 'tableNr';

  componentDidMount() {
    this.validateQueryParams();

    const { changeGuest, changeTable } = this.props;
    const tableNr = this.getTableNrFromLocation();
    const guestNr = this.getGuestNrFromLocation();

    changeGuest(guestNr);
    changeTable(tableNr);
  }

  componentDidUpdate() {
    this.validateQueryParams();
    const { browserState } = this.props;
    const { guestNr, tableNr } = browserState;

    this.setGuestNrToLocation(guestNr);
    this.setTableNrToLocation(tableNr);
  }

  validateQueryParams() {
    const { history } = this.props;
    const location = history.location;
    const { search } = location;

    const searchParams = new URLSearchParams(search);

    const tableNr = searchParams.get(this.tableNrParameter);
    const guestNr = searchParams.get(this.guestNrParameter);

    const validTableNr = tableNr == null || tableNr === '' ? '1' : tableNr;
    const validGuestNr = guestNr == null || guestNr === '' ? '1' : guestNr;

    history.push(
      `?${this.tableNrParameter}=${validTableNr}&${this.guestNrParameter}=${validGuestNr}`
    );
  }

  getGuestNrFromLocation(): string | null {
    const { history } = this.props;
    const location = history.location;
    const { search } = location;

    const searchParams = new URLSearchParams(search);

    const guestNr = searchParams.get(this.guestNrParameter);

    return guestNr;
  }

  setGuestNrToLocation(newGuestNr: string): void {
    const { history } = this.props;
    const tableNr = this.getTableNrFromLocation();

    history.push(
      `?${this.tableNrParameter}=${tableNr}&${this.guestNrParameter}=${newGuestNr}`
    );
  }

  setTableNrToLocation(newTableNr: string): void {
    const { history } = this.props;
    const guestNr = this.getGuestNrFromLocation();

    history.push(
      `?${this.tableNrParameter}=${newTableNr}&${this.guestNrParameter}=${guestNr}`
    );
  }

  getTableNrFromLocation(): string | null {
    const { history } = this.props;
    const location = history.location;
    const { search } = location;

    const searchParams = new URLSearchParams(search);

    const guestNr = searchParams.get(this.tableNrParameter);

    return guestNr;
  }

  render() {
    return <span />;
  }
}

interface StateFromProps {
  browserState: BrowserState;
}

function mapStateToProps(state: AppgustoState): StateFromProps {
  const { browserState } = state;
  return {
    browserState
  };
}

interface DispatchFromProps {
  changeGuest: Function;
  changePerson: Function;
  changeTable: Function;
}

function mapDispatchToProps(dispatch: Dispatch<AnyAction>): DispatchFromProps {
  const actions = {
    changeGuest: BrowserActionCreators.changeGuest,
    changePerson: BrowserActionCreators.changePerson,
    changeTable: BrowserActionCreators.changeTable
  };

  return bindActionCreators(actions, dispatch);
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LocationHandler);



