import { createStore, applyMiddleware, combineReducers, AnyAction, DeepPartial } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import createSagaMiddleware from 'redux-saga';

import { AppgustoState } from './State';
import { OrderReducer, orderSaga } from '../order/api';
import { MenuReducer, menuSaga } from '../menu/api';
import { BrowserReducer } from '../browser/api';

const RootReducer = (state: AppgustoState, action: AnyAction) => {
  const appReducer = combineReducers({
    browserState: BrowserReducer,
    menuState: MenuReducer,
    orderState: OrderReducer
  });
  return appReducer(state, action);
};

const sagaMiddleware = createSagaMiddleware();

function configureStore(initialState: AppgustoState) {
  const middleware = [sagaMiddleware];

  const store = createStore(
    RootReducer,
    initialState as DeepPartial<{}>,
    composeWithDevTools(applyMiddleware(...middleware))
  );

  return store;
}

const Store = configureStore(AppgustoState.INITIAL_STATE);

sagaMiddleware.run(menuSaga);
sagaMiddleware.run(orderSaga);

export default Store;



