import { Order } from './OrderState';
import { FoodItem } from 'src/menu/api';

export const REQUEST_ORDER = 'REQUEST_ORDER';
export const RECEIVE_ORDER = 'RECEIVE_ORDER';
export const INVALIDATE_ORDER = 'INVALIDATE_ORDER';
export const ERROR_ORDER = 'ERROR_ORDER';

export const REQUEST_ORDER_FOOD = 'REQUEST_ORDER_FOOD';
export const RECEIVE_ORDER_FOOD = 'RECEIVE_ORDER_FOOD';

export const REQUEST_CANCEL_FOOD = 'REQUEST_CANCEL_FOOD';
export const RECEIVE_CANCEL_FOOD = 'RECEIVE_CANCEL_FOOD';

export const REQUEST_ORDER_TO_PREP = 'REQUEST_ORDER_TO_PREP';
export const RECEIVE_ORDER_TO_PREP = 'RECEIVE_ORDER_TO_PREP';

export const REQUEST_CLOSE_ORDER = 'REQUEST_CLOSE_ORDER';
export const RECEIVE_CLOSE_ORDER = 'RECEIVE_CLOSE_ORDER';

export type OrderActions = {
  REQUEST_ORDER: {
    type: typeof REQUEST_ORDER,
    payload: {
      tableNr: number,
      personNr: number
    }
  },

  RECEIVE_ORDER: {
    type: typeof RECEIVE_ORDER,
    payload: {
      order: Order,
      lastUpdated: number;
    }
  },

  INVALIDATE_ORDER: {
    type: typeof INVALIDATE_ORDER,
  },

  ERROR_ORDER: {
    type: typeof ERROR_ORDER,
    payload: Error,
    error: boolean
  },

  REQUEST_ORDER_FOOD: {
    type: typeof REQUEST_ORDER_FOOD,
    payload: {
      guestNr: number,
      foodNr: number,
      tableNr: number
    }
  },

  RECEIVE_ORDER_FOOD: {
    type: typeof RECEIVE_ORDER_FOOD,
    payload: {
      food: FoodItem,
      lastUpdated: number;
    }
  },

  REQUEST_CANCEL_FOOD: {
    type: typeof REQUEST_CANCEL_FOOD,
    payload: {
      tableNr: number,
      positionNr: number
    }
  },

  RECEIVE_CANCEL_FOOD: {
    type: typeof RECEIVE_CANCEL_FOOD
  },

  REQUEST_ORDER_TO_PREP: {
    type: typeof REQUEST_ORDER_TO_PREP,
    payload: {
      tableNr: number;
    }
  },

  RECEIVE_ORDER_TO_PREP: {
    type: typeof RECEIVE_ORDER_TO_PREP,
    payload: {
      lastUpdated: number
    }
  },

  REQUEST_CLOSE_ORDER: {
    type: typeof REQUEST_CLOSE_ORDER,
    payload: {
      tableNr: number;
    }
  },

  RECEIVE_CLOSE_ORDER: {
    type: typeof RECEIVE_CLOSE_ORDER,
    payload: {
      lastUpdated: number
    }
  }
};

export const OrderActionCreators = {
  requestOrder: (tableNr: number, personNr: number): OrderActions[typeof REQUEST_ORDER] => {
    return {
      type: REQUEST_ORDER,
      payload: {
        tableNr: tableNr,
        personNr: personNr
      }
    };
  },

  receiveOrder: (order: Order): OrderActions[typeof RECEIVE_ORDER] => {
    return {
      type: RECEIVE_ORDER,
      payload: {
        order,
        lastUpdated: Date.now()
      }
    };
  },

  invalidateOrder: (): OrderActions[typeof INVALIDATE_ORDER] => {
    return {
      type: INVALIDATE_ORDER
    };
  },

  errorOrder: (message: string): OrderActions[typeof ERROR_ORDER] => {
    return {
      type: ERROR_ORDER,
      payload: new Error(message),
      error: true
    };
  },

  requestOrderFood: (guestNr: number, tableNr: number, foodNr: number): OrderActions[typeof REQUEST_ORDER_FOOD] => {
    return {
      type: REQUEST_ORDER_FOOD,
      payload: {
        guestNr,
        foodNr,
        tableNr
      }
    };
  },

  receiveOrderFood: (food: FoodItem): OrderActions[typeof RECEIVE_ORDER_FOOD] => {
    return {
      type: RECEIVE_ORDER_FOOD,
      payload: {
        food,
        lastUpdated: Date.now()
      }
    };
  },

  requestCancelFood: (tableNr: number, positionNr: number): OrderActions[typeof REQUEST_CANCEL_FOOD] => {
    return {
      type: REQUEST_CANCEL_FOOD,
      payload: {
        tableNr,
        positionNr
      }
    };
  },

  receiveCancelFood: (): OrderActions[typeof RECEIVE_CANCEL_FOOD] => {
    return {
      type: RECEIVE_CANCEL_FOOD
    };
  },

  requestOrderToPrep: (tableNr: number): OrderActions[typeof REQUEST_ORDER_TO_PREP] => {
    return {
      type: REQUEST_ORDER_TO_PREP,
      payload: {
        tableNr
      }
    };
  },

  receiveOrderToPrep: (): OrderActions[typeof RECEIVE_ORDER_TO_PREP] => {
    return {
      type: RECEIVE_ORDER_TO_PREP,
      payload: {
        lastUpdated: Date.now()
      }
    };
  },

  requestCloseOrder: (tableNr: number): OrderActions[typeof REQUEST_CLOSE_ORDER] => {
    return {
      type: REQUEST_CLOSE_ORDER,
      payload: {
        tableNr
      }
    };
  },

  receiveCloseOrder: (): OrderActions[typeof RECEIVE_CLOSE_ORDER] => {
    return {
      type: RECEIVE_CLOSE_ORDER,
      payload: {
        lastUpdated: Date.now()
      }
    };
  }
};


