import * as React from 'react';
import { Navbar } from 'react-bootstrap';

function Header() {
  return (
    <header>
      <Navbar bg="dark">
        <Navbar.Brand>AppGusto</Navbar.Brand>
        <Navbar.Toggle />
      </Navbar>
    </header>
  );
}

export default Header;



