import * as React from 'react';
import { Col, Row } from 'react-bootstrap';
import { bindActionCreators, Dispatch, AnyAction } from 'redux';
import { connect } from 'react-redux';

import { OrderActionCreators, OrderState } from './index';
import { AppgustoState } from '../../store/State';
import OrderTable from '../components/OrderTable';
import { BrowserState } from '../../browser/impl/BrowserState';
import { withErrorBoundary } from 'src/shared';

interface OrderContainerProps {
  browserState: BrowserState;
  orderState: OrderState;
  requestOrder: Function;
  requestCancelFood: Function;
}

class OrderContainer extends React.Component<OrderContainerProps> {
  constructor(props: OrderContainerProps) {
    super(props);

    this.handleCancelFood = this.handleCancelFood.bind(this);
  }

  componentDidUpdate() {
    const { browserState, orderState, requestOrder } = this.props;
    const { tableNr, personNr } = browserState;

    if (orderState.invalid) {
      requestOrder(tableNr, personNr);
    }
  }

  handleCancelFood(event: Event, positionNr: number) {
    if (event) {
      event.preventDefault();
    }

    const { browserState, requestCancelFood } = this.props;
    const { tableNr } = browserState;

    requestCancelFood(tableNr, positionNr);
  }

  render() {
    const { orderState } = this.props;
    const { order } = orderState;

    const orderPositions = order == null ? [] : order.speisen;

    return (
      <Row>
        <Col md={12}>
          <OrderTable
            orderPositions={orderPositions}
            onCancelFood={this.handleCancelFood}
          />
        </Col>
      </Row>
    );
  }
}

interface StateFromProps {
  browserState: BrowserState;
  orderState: OrderState;
}

function mapStateToProps(state: AppgustoState): StateFromProps {
  const { browserState, orderState } = state;
  return {
    browserState,
    orderState
  };
}

interface DispatchFromProps {
  requestOrder: Function;
  requestCancelFood: Function;
}

function mapDispatchToProps(dispatch: Dispatch<AnyAction>): DispatchFromProps {
  const actions = {
    requestOrder: OrderActionCreators.requestOrder,
    requestCancelFood: OrderActionCreators.requestCancelFood
  };
  return bindActionCreators(actions, dispatch);
}

export default withErrorBoundary(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(OrderContainer)
);



