export class OrderState {
  static INITIAL_STATE: OrderState = {
    order: null,
    error: null,
    invalid: true,
    isFetching: false,
    lastUpdated: -1
  };
  order: Order | null;
  error: Error | null;
  invalid: boolean;
  isFetching: boolean;
  lastUpdated: number;
}

export class Order {
  id: number;
  nummer: number;
  tischNr: number;
  status: OrderStatus;
  personalId: number;
  bestelldatum: number;
  speisen: OrderPosition[];
}

export enum OrderStatus {
  OFFEN,
  ABGERECHNET
}

export class OrderPosition {
  id: number;
  nummer: number;
  bezeichnung: string;
  gast: number;
  bestellzeit: number;
  verkaufspreis: number;
  bestellungId: number;
  speiseId: number;
  status: OrderPositionStatus;
}

export enum OrderPositionStatus {
  BESTELLT,
  IN_ZUBEREITUNG,
  BEREITGESTELLT,
  SERVIERT,
  STORNIERT
}


