import * as React from 'react';
import { Button, Col, Row } from 'react-bootstrap';
import ChangeGuestModal from 'src/tables/impl/ChangeGuestModal';
import ChangeTableModal from 'src/tables/impl/ChangeTableModal';
import { History } from 'history';
import { OrderActionCreators } from 'src/order/api';
import { Dispatch, bindActionCreators, AnyAction } from 'redux';
import { connect } from 'react-redux';
import { AppgustoState } from 'src/store/State';
import { BrowserActionCreators, BrowserState } from 'src/browser/api';
import { ErrorBoundary } from 'src/shared';

interface TableContainerState {
  guestModalVisible: boolean;
  tableModalVisible: boolean;
}

interface TableContainerProps {
  browserState: BrowserState;
  history: History;
  requestOrder: Function;
  changeGuest: Function;
  changeTable: Function;
}

class TableContainer extends React.Component<
  TableContainerProps,
  TableContainerState
> {
  constructor(props: TableContainerProps) {
    super(props);

    this.state = {
      guestModalVisible: false,
      tableModalVisible: false
    };

    this.handleChangeGuestClick = this.handleChangeGuestClick.bind(this);
    this.handleChangeGuestClose = this.handleChangeGuestClose.bind(this);
    this.handleChangeGuestSubmit = this.handleChangeGuestSubmit.bind(this);

    this.handleChangeTableClick = this.handleChangeTableClick.bind(this);
    this.handleChangeTableClose = this.handleChangeTableClose.bind(this);
    this.handleChangeTableSubmit = this.handleChangeTableSubmit.bind(this);
  }

  handleChangeGuestClick(event: React.MouseEvent<HTMLElement>) {
    if (event) {
      event.preventDefault();
    }

    this.setState({
      guestModalVisible: true
    });
  }

  handleChangeGuestClose(event: Event) {
    if (event) {
      event.preventDefault();
    }

    this.setState({
      guestModalVisible: false
    });
  }

  handleChangeGuestSubmit(event: Event, newGuestNr: string) {
    if (event) {
      event.preventDefault();
    }

    const { changeGuest } = this.props;
    changeGuest(newGuestNr);

    this.setState({
      guestModalVisible: false
    });
  }

  handleChangeTableClick(event: React.MouseEvent<HTMLElement>) {
    if (event) {
      event.preventDefault();
    }

    this.setState({
      tableModalVisible: true
    });
  }

  handleChangeTableClose(event: Event) {
    if (event) {
      event.preventDefault();
    }

    this.setState({
      tableModalVisible: false
    });
  }

  handleChangeTableSubmit(event: Event, newTableNr: string) {
    if (event) {
      event.preventDefault();
    }

    const { changeTable } = this.props;
    changeTable(newTableNr);

    this.setState({
      tableModalVisible: false
    });

    const { browserState, requestOrder } = this.props;
    const { personNr } = browserState;

    if (newTableNr != null && newTableNr !== '') {
      requestOrder(newTableNr, personNr);
    }
  }

  render() {
    const { guestModalVisible, tableModalVisible } = this.state;
    const { browserState } = this.props;
    const { tableNr, guestNr } = browserState;

    return (
      <ErrorBoundary>
        <Row>
          <Col md={4}>
            <span className="h3">Chefkellner</span>
          </Col>
          <Col md={{ span: 1, offset: 1 }}>
            <span className="h3 text-right">Gast</span>
          </Col>
          <Col md={2}>
            <Button
              size="sm"
              block={true}
              onClick={(event: React.MouseEvent<HTMLElement>) =>
                this.handleChangeGuestClick(event)
              }
            >
              <b>{guestNr}</b>
            </Button>
          </Col>
          <Col md={{ span: 1, offset: 1 }}>
            <span className="h3 text-right">Tisch</span>
          </Col>
          <Col md={2}>
            <Button
              size="sm"
              block={true}
              onClick={(event: React.MouseEvent<HTMLElement>) =>
                this.handleChangeTableClick(event)
              }
            >
              <b>{tableNr}</b>
            </Button>
          </Col>
        </Row>

        <ChangeGuestModal
          show={guestModalVisible}
          onClose={this.handleChangeGuestClose}
          onSubmit={this.handleChangeGuestSubmit}
          guestNr={guestNr}
        />

        <ChangeTableModal
          show={tableModalVisible}
          onClose={this.handleChangeTableClose}
          onSubmit={this.handleChangeTableSubmit}
          tableNr={tableNr}
        />
      </ErrorBoundary>
    );
  }
}

interface StateFromProps {
  browserState: BrowserState;
}

function mapStateToProps(state: AppgustoState): StateFromProps {
  const { browserState } = state;

  return {
    browserState
  };
}

interface DispatchFromProps {
  requestOrder: Function;
  changeGuest: Function;
  changeTable: Function;
}

function mapDispatchToProps(dispatch: Dispatch<AnyAction>): DispatchFromProps {
  const actions = {
    requestOrder: OrderActionCreators.requestOrder,
    changeGuest: BrowserActionCreators.changeGuest,
    changeTable: BrowserActionCreators.changeTable
  };
  return bindActionCreators(actions, dispatch);
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TableContainer);



