import * as React from 'react';
import { Col, Row } from 'react-bootstrap';
import { bindActionCreators, Dispatch, AnyAction } from 'redux';
import { connect } from 'react-redux';

import { MenuActionCreators } from 'src/menu/impl/MenuActions';
import MenuCategories from 'src/menu/impl/MenuCategories';
import MenuContent from 'src/menu/impl/MenuContent';
import { AppgustoState } from 'src/store/State';
import { FoodItem, FoodCategory } from 'src/menu/impl/FoodItem';
import { MenuState } from 'src/menu/impl/MenuState';
import { BrowserState } from 'src/browser/api';
import { OrderActionCreators } from 'src/order/api';
import { withErrorBoundary } from 'src/shared';

interface MenuContainerProps {
  browserState: BrowserState;
  menuState: MenuState;
  requestMenu: Function;
  requestOrderFood: Function;
}

interface MenuContainerState {
  selectedCategory: FoodCategory;
}

class MenuContainer extends React.Component<
  MenuContainerProps,
  MenuContainerState
> {
  constructor(props: MenuContainerProps) {
    super(props);

    this.state = {
      selectedCategory: FoodCategory.GETRAENK
    };

    this.changeCategory = this.changeCategory.bind(this);
    this.selectItem = this.selectItem.bind(this);
  }

  componentDidMount() {
    const { requestMenu } = this.props;
    requestMenu();
  }

  changeCategory(category: FoodCategory) {
    this.setState({
      selectedCategory: category
    });
  }

  selectItem(foodItem: FoodItem) {
    const { browserState, requestOrderFood } = this.props;
    const { guestNr, tableNr } = browserState;

    requestOrderFood(guestNr, tableNr, foodItem.nummer);
  }

  getSelectedFoodItems(): FoodItem[] {
    const { selectedCategory } = this.state;
    const menu = this.props.menuState.menu;

    if (menu == null) {
      throw 'Menu not available!';
    }

    switch (selectedCategory) {
      case FoodCategory.GETRAENK:
        return menu.getraenke;
      case FoodCategory.HAUPTSPEISE:
        return menu.hauptspeisen;
      case FoodCategory.NACHSPEISE:
        return menu.nachspeisen;
      case FoodCategory.VORSPEISE:
        return menu.vorspeisen;
      default:
        return menu.getraenke; // should never happen
    }
  }

  render() {
    const foodItems: FoodItem[] = this.getSelectedFoodItems();
    const { selectedCategory } = this.state;

    return (
      <Row>
        <Col md={12}>
          <MenuContent foodItems={foodItems} selectItem={this.selectItem} />
          <hr />
          <MenuCategories
            selectedCategory={selectedCategory}
            changeCategory={this.changeCategory}
          />
        </Col>
      </Row>
    );
  }
}

interface StateFromProps {
  browserState: BrowserState;
  menuState: MenuState;
}

function mapStateToProps(state: AppgustoState): StateFromProps {
  const { browserState, menuState } = state;
  return {
    browserState,
    menuState
  };
}

interface DispatchFromProps {
  requestMenu: Function;
  requestOrderFood: Function;
}

function mapDispatchToProps(dispatch: Dispatch<AnyAction>): DispatchFromProps {
  const actions = {
    requestMenu: MenuActionCreators.requestMenu,
    requestOrderFood: OrderActionCreators.requestOrderFood
  };

  return bindActionCreators(actions, dispatch);
}

export default withErrorBoundary(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(MenuContainer)
);



