import {
  REQUEST_MENU,
  RECEIVE_MENU,
  ERROR_MENU,
  INVALIDATE_MENU
} from './MenuActions';
import { MenuState } from './MenuState';
import { AnyAction } from 'redux';

const initialState: MenuState = MenuState.INITIAL_STATE;

function MenuReducer(state: MenuState = initialState, action: AnyAction): MenuState {
  const { type, payload } = action;

  switch (type) {
    case ERROR_MENU:
      const { error } = payload;
      return {
        ...state,
        error: error.message,
        invalid: false,
        isFetching: false
      };
    case INVALIDATE_MENU:
      return MenuState.INITIAL_STATE;
    case RECEIVE_MENU:
      const { lastUpdated, menu } = payload;
      return {
        ...state,
        menu,
        error: null,
        invalid: false,
        isFetching: false,
        lastUpdated
      };
    case REQUEST_MENU:
      return {
        ...state,
        error: null,
        invalid: false,
        isFetching: true
      };
    default:
      return state;
  }
}

export default MenuReducer;



