import * as React from 'react';
import { Button, Col, Row } from 'react-bootstrap';
import { BrowserState } from '../../browser/api';
import { AppgustoState } from '../../store/State';
import { Dispatch, bindActionCreators, AnyAction } from 'redux';
import { connect } from 'react-redux';
import { OrderActionCreators } from '../components/OrderActions';
import { withErrorBoundary } from 'src/shared';

interface ActionContainerProps {
  browserState: BrowserState;
  requestOrderToPrep: Function;
  requestCloseOrder: Function;
}

class ActionContainer extends React.Component<ActionContainerProps> {
  constructor(props: ActionContainerProps) {
    super(props);

    this.foodToPrep = this.foodToPrep.bind(this);
    this.closeOrder = this.closeOrder.bind(this);
  }

  foodToPrep(event: React.MouseEvent<HTMLElement>) {
    if (event) {
      event.preventDefault();
    }

    const { browserState, requestOrderToPrep } = this.props;
    const { tableNr } = browserState;
    requestOrderToPrep(tableNr);
  }

  closeOrder(event: React.MouseEvent<HTMLElement>) {
    if (event) {
      event.preventDefault();
    }

    const { browserState, requestCloseOrder } = this.props;
    const { tableNr } = browserState;
    requestCloseOrder(tableNr);
  }

  render() {
    return (
      <Row>
        <Col md={3}>
          <Button
            size="lg"
            variant="primary"
            block={true}
            onClick={(event: React.MouseEvent<HTMLElement>) =>
              this.foodToPrep(event)
            }
          >
            Zur Kche/Bar
          </Button>
        </Col>

        <Col md={{ span: 3, offset: 6 }}>
          <Button
            size="lg"
            variant="success"
            block={true}
            onClick={(event: React.MouseEvent<HTMLElement>) =>
              this.closeOrder(event)
            }
          >
            Rechnung
          </Button>
        </Col>
      </Row>
    );
  }
}

interface StateFromProps {
  browserState: BrowserState;
}

function mapStateToProps(state: AppgustoState): StateFromProps {
  const { browserState } = state;
  return {
    browserState
  };
}

interface DispatchFromProps {
  requestOrderToPrep: Function;
  requestCloseOrder: Function;
}

function mapDispatchToProps(dispatch: Dispatch<AnyAction>): DispatchFromProps {
  const actions = {
    requestOrderToPrep: OrderActionCreators.requestOrderToPrep,
    requestCloseOrder: OrderActionCreators.requestCloseOrder
  };

  return bindActionCreators(actions, dispatch);
}

export default withErrorBoundary(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(ActionContainer)
);



