import { FoodItem } from 'src/menu/impl/FoodItem';

export class MenuState {
  static INITIAL_STATE: MenuState = {
    menu: {
      getraenke: [],
      hauptspeisen: [],
      nachspeisen: [],
      speiskartenName: '',
      vorspeisen: []
    },
    error: null,
    invalid: true,
    isFetching: false,
    lastUpdated: -1
  };
  menu: Menu;
  error: Error | null;
  invalid: boolean;
  isFetching: boolean;
  lastUpdated: number;
}

export class Menu {
  getraenke: FoodItem[];
  hauptspeisen: FoodItem[];
  nachspeisen: FoodItem[];
  speiskartenName: string;
  vorspeisen: FoodItem[];
}



