import { Api } from 'src/backend/api';
import { Menu } from './MenuState';

export function fetchMenu(): Promise<Menu> {
  return Api.get('/speisekarte', '').then(
    ({ status, json }) => { return json; },
    ({ status, error }) => {
      console.error(error);
      return error;
    }
  );
}


