import { Api } from 'src/backend/api';
import { FoodItem } from 'src/menu/api';
import { Order } from './OrderState';

export function fetchOrder(tableNr: number, personNr: number): Promise<Order> {
  return Api.get(`/tische/${tableNr}?personalNr=${personNr}`, '').then(
    ({ status, json }) => {
      return json;
    },
    ({ status, error }) => {
      console.error(error);
      return error;
    }
  );
}

export function fetchOrderFood(
  guestNr: number,
  tableNr: number,
  foodNr: number
): Promise<FoodItem> {
  const payload = {
    gast: guestNr,
    speiseNr: foodNr,
    wuensche: '',
  };

  return Api.post(`/tische/${tableNr}/speisen`, payload, '').then(
    ({ status, json }) => {
      return json;
    },
    ({ status, error }) => {
      console.error(error);
      return error;
    }
  );
}

export function fetchCancelFood(
  tableNr: number,
  positionNr: number
): Promise<FoodItem> {
  return Api.post(
    `/tische/delete/${tableNr}/speisen/${positionNr}`,
    {},
    ''
  ).then(
    ({ status, json }) => {
      return json;
    },
    ({ status, error }) => {
      console.error(error);
      return error;
    }
  );
}

export function fetchFoodToPreparation(tableNr: number): Promise<void> {
  return Api.post(`/tische/${tableNr}`, {}, '').then(
    ({ status, json }) => {
      return json;
    },
    ({ status, error }) => {
      console.error(error);
      return error;
    }
  );
}

export function fetchCloseOrder(tableNr: number): Promise<void> {
  return Api.post(`/tische/delete/${tableNr}`, {}, '').then(
    ({ status, json }) => {
      return json;
    },
    ({ status, error }) => {
      console.error(error);
      return error;
    }
  );
}



