import { BrowserState } from 'src/browser/impl/BrowserState';
import { AnyAction } from 'redux';
import {
  CHANGE_GUEST,
  CHANGE_PERSON,
  CHANGE_TABLE
} from 'src/browser/impl/BrowserActions';

const initialState: BrowserState = BrowserState.INITIAL_STATE;

function BrowserReducer(state: BrowserState = initialState, action: AnyAction) {
  const { type, payload } = action;

  switch (type) {
    case CHANGE_GUEST:
      let { guestNr } = payload;
      if (guestNr == null || guestNr === '') {
        guestNr = '1';
      }
      return {
        ...state,
        guestNr
      };

    case CHANGE_PERSON:
      const { personNr } = payload;
      return {
        ...state,
        personNr
      };

    case CHANGE_TABLE:
      let { tableNr } = payload;
      if (tableNr == null || tableNr === '') {
        tableNr = '1';
      }
      return {
        ...state,
        tableNr
      };

    default:
      return state;
  }
}

export default BrowserReducer;



