import * as React from 'react';

const loading = require('./loading.svg');

interface LoadingProps {
  width: number;
  height: number;
}

const Loading: React.SFC<LoadingProps> = ({ width, height }) => {
  return (
    <img
      id="loading"
      src={loading}
      alt="Loading..."
      width={width}
      height={height}
    />
  );
};

export default Loading;



