import * as React from 'react';
import { Button, Col, Row, Modal } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import { MouseEvent } from 'react';

interface ChangeGuestModalProps {
  show: boolean;
  guestNr: string | null;
  onClose: Function;
  onSubmit: Function;
}

interface ChangeGuestModalState {
  newGuestNr: string;
}

class ChangeGuestModal extends React.Component<
  ChangeGuestModalProps,
  ChangeGuestModalState
> {
  constructor(props: ChangeGuestModalProps) {
    super(props);

    const { guestNr } = this.props;

    this.state = {
      newGuestNr: guestNr == null ? '1' : guestNr
    };

    this.close = this.close.bind(this);
  }

  componentDidUpdate(prevProps: ChangeGuestModalProps) {
    const { guestNr } = this.props;

    if (guestNr !== prevProps.guestNr) {
      this.setState({
        newGuestNr: guestNr == null ? '1' : guestNr
      });
    }
  }

  reset() {
    const { guestNr } = this.props;
    this.setState({
      newGuestNr: guestNr == null ? '1' : guestNr
    });
  }

  close() {
    const { onClose } = this.props;

    onClose();
    this.reset();
  }

  submit(event: MouseEvent<HTMLElement>) {
    const { onSubmit } = this.props;
    const { newGuestNr } = this.state;

    onSubmit(event, newGuestNr);
    this.reset();
  }

  appendDigit(digit: number) {
    const { newGuestNr } = this.state;
    const updatedGuestNr = `${newGuestNr}${digit}`;

    this.setState({
      newGuestNr: updatedGuestNr
    });
  }

  removeLastDigit() {
    const { newGuestNr } = this.state;
    if (newGuestNr.length > 0) {
      const updatedGuestNr = newGuestNr.slice(0, -1); // remove last digit
      this.setState({
        newGuestNr: updatedGuestNr
      });
    }
  }

  newNumberIsValid() {
    const { newGuestNr } = this.state;

    const guestNrAsInt = parseInt(newGuestNr, 10);

    return guestNrAsInt % 1 === 0 && newGuestNr.length > 0;
  }

  render() {
    const { show } = this.props;
    const { newGuestNr } = this.state;

    return (
      <Modal
        show={show}
        animation={false}
        onHide={this.close}
        id="modal-change-guest"
      >
        <Modal.Header closeButton={true}>Anderen Gast whlen</Modal.Header>

        <Modal.Body>
          <Row>
            <Col md={{ span: 6, offset: 3 }} className="text-center">
              <h2>{newGuestNr === '' ? 0 : newGuestNr}</h2>
            </Col>
          </Row>

          <Row>
            <Col md={{ span: 2, offset: 3 }}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(1)
                }
              >
                1
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(2)
                }
              >
                2
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(3)
                }
              >
                3
              </Button>
            </Col>
          </Row>

          <Row>
            <Col md={{ span: 2, offset: 3 }}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(4)
                }
              >
                4
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(5)
                }
              >
                5
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(6)
                }
              >
                6
              </Button>
            </Col>
          </Row>

          <Row>
            <Col md={{ span: 2, offset: 3 }}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(7)
                }
              >
                7
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(8)
                }
              >
                8
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(9)
                }
              >
                9
              </Button>
            </Col>
          </Row>

          <Row>
            <Col md={{ span: 2, offset: 5 }}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(0)
                }
              >
                0
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.removeLastDigit()
                }
              >
                <FontAwesomeIcon icon={faArrowLeft} />
              </Button>
            </Col>
          </Row>
        </Modal.Body>

        <Modal.Footer>
          <Row>
            <Col md={6}>
              <Button
                onClick={(event: React.MouseEvent<HTMLElement>) => this.close()}
              >
                zurck
              </Button>
            </Col>
            <Col md={6}>
              <Button
                variant="primary"
                active={this.newNumberIsValid()}
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.submit(event)
                }
              >
                enter
              </Button>
            </Col>
          </Row>
        </Modal.Footer>
      </Modal>
    );
  }
}

export default ChangeGuestModal;



