import { OrderState, Order } from './OrderState';
import { AnyAction } from 'redux';
import {
  ERROR_ORDER,
  INVALIDATE_ORDER,
  RECEIVE_ORDER,
  REQUEST_ORDER,
  REQUEST_ORDER_FOOD,
  RECEIVE_ORDER_FOOD,
  REQUEST_ORDER_TO_PREP,
  RECEIVE_ORDER_TO_PREP,
  REQUEST_CLOSE_ORDER,
  RECEIVE_CLOSE_ORDER,
  REQUEST_CANCEL_FOOD,
  RECEIVE_CANCEL_FOOD
} from './OrderActions';

const initialState: OrderState = OrderState.INITIAL_STATE;

function OrderReducer(state: OrderState = initialState, action: AnyAction) {
  const { type, payload } = action;

  switch (type) {

    case ERROR_ORDER:
      const { error } = payload;
      return {
        ...state,
        error: error.message,
        invalid: false,
        isFetching: false
      };

    case INVALIDATE_ORDER:
      return OrderState.INITIAL_STATE;

    case RECEIVE_ORDER:
      const { lastUpdated, order } = payload;

      return {
        ...state,
        order,
        error: null,
        invalid: false,
        isFetching: false,
        lastUpdated
      };

    case REQUEST_ORDER:
      return {
        ...state,
        error: null,
        invalid: false,
        isFetching: true
      };

    case REQUEST_ORDER_FOOD:
      return {
        ...state,
        error: null,
        invalid: false,
        isFetching: true
      };

    case RECEIVE_ORDER_FOOD:
      const { food } = payload;

      const currentOrder: Order | null = state.order;
      const speisen = currentOrder == null ? [] : currentOrder.speisen;

      return {
        ...state,
        order: {
          ...currentOrder,
          speisen: [...speisen, food]
        },
        error: null,
        invalid: false,
        isFetching: false,
        lastUpdated: payload.lastUpdated
      };

    case REQUEST_CANCEL_FOOD:
      return {
        ...state,
        error: null,
        invalid: false,
        isFetching: true
      };

    case RECEIVE_CANCEL_FOOD:
      return {
        ...state,
        error: null,
        invalid: true,
        isFetching: false
      };

    case REQUEST_ORDER_TO_PREP:
      return {
        ...state,
        error: null,
        invalid: false,
        isFetching: true
      };

    case RECEIVE_ORDER_TO_PREP:
      return {
        ...state,
        error: null,
        invalid: true,
        isFetching: false,
        lastUpdated: payload.lastUpdated
      };

    case REQUEST_CLOSE_ORDER:
      return {
        ...state,
        error: null,
        invalid: false,
        isFetching: true
      };

    case RECEIVE_CLOSE_ORDER:
      return {
        ...state,
        error: null,
        invalid: true,
        isFetching: false,
        lastUpdated: payload.lastUpdated
      };

    default:
      return state;
  }
}

export default OrderReducer;


