const API_ROOT = '/api';

function prepareFetchConfig(method: string, payload: object | null, token: string) {
  let headers = {
    Accept: 'application/json',
    'Content-Type': 'application/json',
    Authorization: ''
  };

  if (token) {
    headers = {
      ...headers,
      Authorization: `Bearer ${token}`
    };
  }

  let config = {
  };

  if (payload) {
    config = {
      method: method,
      headers,
      body: JSON.stringify(payload)
    };
  } else {
    config = {
      method: method,
      headers
    };
  }

  return config;
}

export function callEndpoint(method: string, endpoint: string, payload: object | null, token: string) {
  const url = `${API_ROOT}${endpoint}`;
  const config = prepareFetchConfig(method, payload, token);

  return fetch(url, config);
}

function callApi(
  method: string = 'get',
  endpoint: string = '',
  payload: object | null = null,
  token: string = ''
) {
  const url = `${API_ROOT}${endpoint}`;
  const config = prepareFetchConfig(method, payload, token);

  return new Promise((resolve, reject) => {
    fetch(url, config)
      // Try to parse the response

      .then(response => {
        // check whether the response contains a json body
        const contentType = response.headers.get('content-type');

        if (contentType && contentType.indexOf('application/json') !== -1) {
          return response.json().then(json => ({
            status: response.status,
            json
          }));
        } else {
          return response.text().then(text => ({
            status: response.status,
            json: null
          }));
        }
      })
      // Both fetching and parsing succeeded!
      .then(result => {
        const { status, json } = result;
        if (status >= 400) {
          // Status looks bad
          return reject({ status, error: json });
        } else {
          // Status looks good
          return resolve({ status, json });
        }
      })
      .catch(error => console.error('Api call error: ', error));
  });
}

/**
 * Make a GET request on the specified endpoint
 *
 * @param {string} endpoint the endpoint relative to the API_ROOT with a preceeding slash. e.g. '/users'.
 * @param {string} token an authentication token (only necessary for protected endpoints).
 */
export function get(endpoint: string, token: string) {
  return callApi('get', endpoint, null, token);
}

/**
 * Make a PUT request on the specified endpoint
 *
 * @param {string} endpoint the endpoint relative to the API_ROOT with a preceeding slash. e.g. '/users'.
 * @param {object} payload the request payload as object.
 * @param {string} token an authentication token (only necessary for protected endpoints).
 */
export function put(endpoint: string, payload: object, token: string) {
  return callApi('put', endpoint, payload, token);
}

/**
 * Make a POST request on the specified endpoint
 *
 * @param {string} endpoint the endpoint relative to the API_ROOT with a preceeding slash. e.g. '/users'.
 * @param {object} payload the request payload as object.
 * @param {string} token an authentication token (only necessary for protected endpoints).
 */
export function post(endpoint: string, payload: object, token: string) {
  return callApi('post', endpoint, payload, token);
}

/**
 * Make a DELETE request on the specified endpoint
 *
 * @param {string} endpoint the endpoint relative to the API_ROOT with a preceeding slash. e.g. '/users'.
 * @param {object} payload the request payload as object.
 * @param {string} token an authentication token (only necessary for protected endpoints).
 */
export function del(endpoint: string, payload: object, token: string) {
  return callApi('delete', endpoint, payload, token);
}



