import { Menu } from './MenuState';

export const REQUEST_MENU = 'REQUEST_MENU';
export const RECEIVE_MENU = 'RECEIVE_MENU';
export const INVALIDATE_MENU = 'INVALIDATE_MENU';
export const ERROR_MENU = 'ERROR_MENU';

export type MenuActions = {
  REQUEST_MENU: {
    type: typeof REQUEST_MENU,
  },
  RECEIVE_MENU: {
    type: typeof RECEIVE_MENU,
    payload: {
      menu: Menu;
      lastUpdated: number;
    }
  },
  INVALIDATE_MENU: {
    type: typeof INVALIDATE_MENU
  },
  ERROR_MENU: {
    type: typeof ERROR_MENU,
    payload: Error,
    error: boolean
  }
};

export const MenuActionCreators = {

  requestMenu: (): MenuActions[typeof REQUEST_MENU] => {
    return {
      type: REQUEST_MENU
    };
  },

  receiveMenu: (menu: Menu): MenuActions[typeof RECEIVE_MENU] => {
    return {
      type: RECEIVE_MENU,
      payload: {
        menu,
        lastUpdated: Date.now()
      }
    };
  },

  invalidateMenu: (): MenuActions[typeof INVALIDATE_MENU] => {
    return {
      type: INVALIDATE_MENU
    };
  },

  errorMenu: (message: string): MenuActions[typeof ERROR_MENU] => {
    return {
      type: ERROR_MENU,
      payload: new Error(message),
      error: true
    };
  }
};


