import { call, put, takeLatest } from 'redux-saga/effects';
import { REQUEST_MENU, MenuActionCreators } from './MenuActions';
import * as MenuService from './MenuService';
import { AnyAction } from 'redux';

function* fetchMenu(action: AnyAction) {
  try {

    const menu = yield call(MenuService.fetchMenu);
    yield put(MenuActionCreators.receiveMenu(menu));
  } catch (e) {
    const errorAction = MenuActionCreators.errorMenu(e.message);
    yield put(errorAction);
  }
}

function* menuSaga() {
  yield takeLatest(REQUEST_MENU, fetchMenu);
}

export default menuSaga;



