import * as React from 'react';
import { Button, Col, Row, Modal } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import { MouseEvent } from 'react';

interface ChangeTableModalProps {
  show: boolean;
  tableNr: string | null;
  onClose: Function;
  onSubmit: Function;
}

interface ChangeTableModalState {
  newTableNr: string;
}

class ChangeTableModal extends React.Component<
  ChangeTableModalProps,
  ChangeTableModalState
> {
  constructor(props: ChangeTableModalProps) {
    super(props);

    const { tableNr } = this.props;

    this.state = {
      newTableNr: tableNr == null ? '1' : tableNr
    };

    this.close = this.close.bind(this);
  }

  componentDidUpdate(prevProps: ChangeTableModalProps) {
    const { tableNr } = this.props;

    if (tableNr !== prevProps.tableNr) {
      this.setState({
        newTableNr: tableNr == null ? '1' : tableNr
      });
    }
  }

  reset() {
    const { tableNr } = this.props;
    this.setState({
      newTableNr: tableNr == null ? '1' : tableNr
    });
  }

  close() {
    const { onClose } = this.props;

    onClose();
    this.reset();
  }

  submit(event: MouseEvent<HTMLElement>) {
    const { onSubmit } = this.props;
    const { newTableNr } = this.state;

    onSubmit(event, newTableNr);
    this.reset();
  }

  appendDigit(digit: number) {
    const { newTableNr } = this.state;
    const updatedTableNr = `${newTableNr}${digit}`;

    this.setState({
      newTableNr: updatedTableNr
    });
  }

  removeLastDigit() {
    const { newTableNr } = this.state;
    if (newTableNr.length > 0) {
      const updatedTableNr = newTableNr.slice(0, -1); // remove last digit
      this.setState({
        newTableNr: updatedTableNr
      });
    }
  }

  newNumberIsValid() {
    const { newTableNr } = this.state;

    const tableNrAsInt = parseInt(newTableNr, 10);

    return tableNrAsInt % 1 === 0 && newTableNr.length > 0;
  }

  render() {
    const { show } = this.props;
    const { newTableNr } = this.state;

    return (
      <Modal
        show={show}
        animation={false}
        onHide={this.close}
        id="modal-change-table"
      >
        <Modal.Header closeButton={true}>Anderen Tisch whlen</Modal.Header>

        <Modal.Body>
          <Row>
            <Col md={{ span: 6, offset: 3 }} className="text-center">
              <h2>{newTableNr === '' ? 0 : newTableNr}</h2>
            </Col>
          </Row>

          <Row>
            <Col md={{ span: 2, offset: 3 }}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(1)
                }
              >
                1
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(2)
                }
              >
                2
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(3)
                }
              >
                3
              </Button>
            </Col>
          </Row>

          <Row>
            <Col md={{ span: 2, offset: 3 }}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(4)
                }
              >
                4
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(5)
                }
              >
                5
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(6)
                }
              >
                6
              </Button>
            </Col>
          </Row>

          <Row>
            <Col md={{ span: 2, offset: 3 }}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(7)
                }
              >
                7
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(8)
                }
              >
                8
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(9)
                }
              >
                9
              </Button>
            </Col>
          </Row>

          <Row>
            <Col md={{ span: 2, offset: 5 }}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.appendDigit(0)
                }
              >
                0
              </Button>
            </Col>
            <Col md={2}>
              <Button
                size="lg"
                href="#"
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.removeLastDigit()
                }
              >
                <FontAwesomeIcon icon={faArrowLeft} />
              </Button>
            </Col>
          </Row>
        </Modal.Body>

        <Modal.Footer>
          <Row>
            <Col md={6}>
              <Button
                onClick={(event: React.MouseEvent<HTMLElement>) => this.close()}
              >
                zurck
              </Button>
            </Col>
            <Col md={6}>
              <Button
                variant="primary"
                active={this.newNumberIsValid()}
                block={true}
                onClick={(event: React.MouseEvent<HTMLElement>) =>
                  this.submit(event)
                }
              >
                enter
              </Button>
            </Col>
          </Row>
        </Modal.Footer>
      </Modal>
    );
  }
}

export default ChangeTableModal;



