import * as React from 'react';
import { Table } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { OrderPosition } from './OrderState';

interface OrderTableProps {
  orderPositions: OrderPosition[];
  onCancelFood: Function;
}

class OrderTable extends React.Component<OrderTableProps> {
  cancelFood(event: React.MouseEvent, positionNr: number) {
    const { onCancelFood } = this.props;

    onCancelFood(event, positionNr);
  }

  render() {
    const { orderPositions } = this.props;

    return (
      <Table striped={true} hover={true}>
        <thead>
          <tr>
            <th>Gast</th>
            <th>Speise</th>
            <th>Preis</th>
            <th>Status</th>
            <th>Aktion</th>
          </tr>
        </thead>
        <tbody>
          {orderPositions.length > 0 ? (
            orderPositions.map(position => {
              return (
                <tr key={position.nummer}>
                  <td>{position.gast}</td>
                  <td>{position.bezeichnung}</td>
                  <td>{position.verkaufspreis} </td>
                  <td>{position.status}</td>
                  <td>
                    {position.status.toString() === 'BESTELLT' ? (
                      <a
                        href=""
                        onClick={(event: React.MouseEvent<HTMLElement>) =>
                          this.cancelFood(event, position.nummer)
                        }
                      >
                        <FontAwesomeIcon icon={faTimes} />
                      </a>
                    ) : (
                      <span />
                    )}
                  </td>
                </tr>
              );
            })
          ) : (
            <tr>
              <td colSpan={5}>Es wurden noch keine Speisen gewhlt.</td>
            </tr>
          )}
        </tbody>
      </Table>
    );
  }
}

export default OrderTable;



