import * as React from 'react';
import { Button, ButtonGroup, Col, Row } from 'react-bootstrap';
import { FoodCategory } from './FoodItem';

interface MenuCategoriesProps {
  selectedCategory: FoodCategory;
  changeCategory: (category: FoodCategory) => void;
}

const MenuCategories: React.StatelessComponent<MenuCategoriesProps> = props => {
  const { changeCategory, selectedCategory } = props;

  return (
    <Row>
      <Col md={12}>
        <ButtonGroup className="d-flex">
          <Button
            size="sm"
            href="#"
            active={selectedCategory === FoodCategory.VORSPEISE}
            onClick={(event: React.MouseEvent<HTMLElement>) =>
              changeCategory(FoodCategory.VORSPEISE)
            }
          >
            Vorspeisen
          </Button>
          <Button
            size="sm"
            href="#"
            active={selectedCategory === FoodCategory.HAUPTSPEISE}
            onClick={(event: React.MouseEvent<HTMLElement>) =>
              changeCategory(FoodCategory.HAUPTSPEISE)
            }
          >
            Hauptspeisen
          </Button>
          <Button
            size="sm"
            href="#"
            active={selectedCategory === FoodCategory.NACHSPEISE}
            onClick={(event: React.MouseEvent<HTMLElement>) =>
              changeCategory(FoodCategory.NACHSPEISE)
            }
          >
            Nachspeisen
          </Button>
          <Button
            size="sm"
            href="#"
            active={selectedCategory === FoodCategory.GETRAENK}
            onClick={(event: React.MouseEvent<HTMLElement>) =>
              changeCategory(FoodCategory.GETRAENK)
            }
          >
            Getrnke
          </Button>
        </ButtonGroup>
      </Col>
    </Row>
  );
};

export default MenuCategories;



