import * as React from 'react';
import { Col, Row } from 'react-bootstrap';
import { connect } from 'react-redux';
import { AppgustoState } from '../../store/State';

interface StoreViewerProps {
  state: AppgustoState;
}

export class StoreViewer extends React.Component<StoreViewerProps> {

  render() {
    const { state } = this.props;

    return (
      <div>
        <Row>
          <Col md={12}>
            <h1>Redux Store</h1>
          </Col>
        </Row>
        <Row>
          <Col md={12}>
            <pre>{JSON.stringify(state, null, 2)}</pre>
          </Col>
        </Row>
      </div>
    );
  }
}

function mapStateToProps(state: AppgustoState) {
  return {
    state
  };
}

export default connect(mapStateToProps)(StoreViewer);



