import { call, put, takeLatest } from 'redux-saga/effects';
import {
  OrderActionCreators,
  REQUEST_ORDER,
  REQUEST_ORDER_FOOD,
  REQUEST_ORDER_TO_PREP,
  REQUEST_CLOSE_ORDER,
  REQUEST_CANCEL_FOOD,
} from './OrderActions';
import { OrderActions } from '../api/index';
import * as OrderService from './OrderService';
import { Order } from './OrderState';
import { FoodItem } from 'src/menu/api';

function* fetchOrder(action: OrderActions[typeof REQUEST_ORDER]) {
  try {
    const { tableNr, personNr } = action.payload;

    const order = (yield call(
      OrderService.fetchOrder,
      tableNr,
      personNr
    )) as Order;
    yield put(OrderActionCreators.receiveOrder(order));
  } catch (e) {
    const errorAction = OrderActionCreators.errorOrder(e.message);
    yield put(errorAction);
  }
}

function* fetchOrderFood(action: OrderActions[typeof REQUEST_ORDER_FOOD]) {
  try {
    const { guestNr, tableNr, foodNr } = action.payload;

    const foodItem = (yield call(
      OrderService.fetchOrderFood,
      guestNr,
      tableNr,
      foodNr
    )) as FoodItem;

    yield put(OrderActionCreators.receiveOrderFood(foodItem));
  } catch (e) {
    const errorAction = OrderActionCreators.errorOrder(e.message);
    yield put(errorAction);
  }
}

function* fetchCancelFood(action: OrderActions[typeof REQUEST_CANCEL_FOOD]) {
  try {
    const { tableNr, positionNr } = action.payload;

    yield call(OrderService.fetchCancelFood, tableNr, positionNr);
    yield put(OrderActionCreators.receiveCancelFood());
  } catch (e) {
    const errorAction = OrderActionCreators.errorOrder(e.message);
    yield put(errorAction);
  }
}

function* fetchOrderToPrep(action: OrderActions[typeof REQUEST_ORDER_TO_PREP]) {
  try {
    const { tableNr } = action.payload;

    yield call(OrderService.fetchFoodToPreparation, tableNr);
    yield put(OrderActionCreators.receiveOrderToPrep());
  } catch (e) {
    const errorAction = OrderActionCreators.errorOrder(e.message);
    yield put(errorAction);
  }
}

function* fetchCloseOrder(action: OrderActions[typeof REQUEST_CLOSE_ORDER]) {
  try {
    const { tableNr } = action.payload;

    yield call(OrderService.fetchCloseOrder, tableNr);
    yield put(OrderActionCreators.receiveCloseOrder());
  } catch (e) {
    const errorAction = OrderActionCreators.errorOrder(e.message);
    yield put(errorAction);
  }
}

function* orderSaga() {
  yield takeLatest(REQUEST_ORDER, fetchOrder);
  yield takeLatest(REQUEST_ORDER_FOOD, fetchOrderFood);
  yield takeLatest(REQUEST_ORDER_TO_PREP, fetchOrderToPrep);
  yield takeLatest(REQUEST_CLOSE_ORDER, fetchCloseOrder);
  yield takeLatest(REQUEST_CANCEL_FOOD, fetchCancelFood);
}

export default orderSaga;



