import * as React from 'react';
import { Container } from 'react-bootstrap';

import { Header } from './shared';
import { LocationHandler } from './browser/api';
import { MenuContainer } from './menu/api';
import { OrderActionContainer, OrderContainer } from './order/api';
import { TableContainer } from './tables/api';
import { Route, Redirect, Switch } from 'react-router';
import { History } from 'history';

interface ContentProps {
  history: History;
}

function Content(props: ContentProps) {
  const { history } = props;

  return (
    <div>
      <Header />
      <Container>
        <TableContainer history={history} />
        <hr />
        <OrderContainer />
        <MenuContainer />
        <hr />
        <OrderActionContainer />
      </Container>
      <LocationHandler history={history} />
    </div>
  );
}

function DefaultRedirect() {
  return <Redirect to="/appgusto" />;
}

function AppGusto() {
  return (
    <Switch>
      <Route path="/appgusto" component={Content} />
      <Route component={DefaultRedirect} />
    </Switch>
  );
}

export default AppGusto;



