import * as React from 'react';
import { Col, Row } from 'react-bootstrap';

interface ErrorBoundaryProps {
  [key: string]: object | null;
}

interface ErrorBoundaryState {
  hasError: boolean;
}

const ErrorBoundaryStyle = {
  border: '2px solid orange',
  borderRadius: '1rem',
  padding: '2rem'
};

class ErrorBoundary extends React.Component<ErrorBoundaryProps, ErrorBoundaryState> {
  constructor(props: ErrorBoundaryProps) {
    super(props);
    this.state = { hasError: false };
  }

  componentDidCatch(error: Error | null, info: object) {
    this.setState({ hasError: true });
  }

  render() {
    if (this.state.hasError) {
      // You can render any custom fallback UI
      return (
        <React.Fragment>
          <Row>
            <Col md={12}>
              <div style={ErrorBoundaryStyle}>
                <h2>Da ist wohl etwas schief gelaufen :(</h2>
                <ul>
                  <li>Luft der Server?</li>
                  <li>Ist die DB befllt?</li>
                  <li>Gibt es einen Fehler in der Browser-Konsole?</li>
                  <li>Gibt es einen Fehler im Netzwerk-Tab?</li>
                </ul>
              </div>
            </Col>
          </Row>
        </React.Fragment>
      );
    }
    return this.props.children;
  }
}

export default ErrorBoundary;


