export const CHANGE_GUEST = 'CHANGE_GUEST';
export const CHANGE_PERSON = 'CHANGE_PERSON';
export const CHANGE_TABLE = 'CHANGE_TABLE';

export type BrowserActions = {
  CHANGE_GUEST: {
    type: typeof CHANGE_GUEST,
    payload: {
      guestNr: string
    }
  },

  CHANGE_PERSON: {
    type: typeof CHANGE_PERSON,
    payload: {
      personNr: string
    }
  },

  CHANGE_TABLE: {
    type: typeof CHANGE_TABLE,
    payload: {
      tableNr: string
    }
  }
};

export const BrowserActionCreators = {

  changeGuest: (guestNr: string): BrowserActions[typeof CHANGE_GUEST] => {
    return {
      type: CHANGE_GUEST,
      payload: {
        guestNr
      }
    };
  },

  changePerson: (personNr: string): BrowserActions[typeof CHANGE_PERSON] => {
    return {
      type: CHANGE_PERSON,
      payload: {
        personNr
      }
    };
  },

  changeTable: (tableNr: string): BrowserActions[typeof CHANGE_TABLE] => {
    return {
      type: CHANGE_TABLE,
      payload: {
        tableNr
      }
    };
  }
};


